@import '/src/assets/styles/main.scss';

.Slider {
  position: relative;

  width: 100%;
  height: 300px;

  border-radius: 5px 5px 0 0;

  &__slide {
    display: flex;
    justify-content: center;
    align-items: center;

    background: #eaeaea;

    text-align: center;
    font-size: 18px;
  }

  &__image {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;

    cursor: pointer;
  }

  &__next {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;

    padding-bottom: 100%;
    width: 60px;

    color: #eaeaea;

    &::after {
      position: absolute;
      top: 150px;
    }

    &:hover {
      background-color: #ffffff3f;
    }
  }

  &__prev {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;

    padding-bottom: 100%;
    width: 60px;

    color: #eaeaea;

    &::after {
      position: absolute;
      top: 150px;
    }

    &:hover {
      background-color: #ffffff3f;
    }
  }
}

.swiper-pagination-bullet-active {
  background-color: #eaeaea;
}

