@import '/src/assets/styles/main.scss';

.Header {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: space-between;
  padding: $space-m;

  background-color: $white-c;

  border-radius: 0 0 $space-s $space-s;
  box-shadow: 0 $space-s $space-s $grey-c;

  @include onNotebook() {
    flex-direction: row;
  }

  &__section {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 15px;

    &:first-child {
      flex-grow: 1;
    }

    &:last-child {
      justify-content: space-between;
    }
  }

  &__logo {
    justify-self: start;
    mix-blend-mode: multiply;

    & > * {
      width: 60px;

      @include onNotebook() {
        width: 80px;
        margin-right: 20px;
      }
    }
  }

  &__search {
    grid-column: 1 / 3;
    flex-grow: 1;
  }

  &__dropdown {
    margin-left: 20px;

    @include onNotebook() {
      margin-left: unset;
      margin-right: 100px;
    }
  }

  &__menu {
    position: relative;

    margin-right: 20px;
  }

  &__button {
    border: 0;

    background-color: transparent;
  }
}
