@import '/src/assets/styles/main.scss';

.App {
  display: flex;
  flex-direction: column;
  height: auto;

  &__section {
    flex-grow: 1;

    padding-bottom: $space-xl;

    &--header {
      position: relative;
      z-index: 3;
      flex-grow: 0;

      @include onNotebook() {
        position: sticky;
        top: 0;
        z-index: 3;
      }
    }

    &--footer {
      justify-self: end;
      border-top: 1px solid $black-c;
      padding-bottom: 0;
      flex-grow: 0;
    }
  }
}
